.swiper-cube .swiper-slide-shadow-right {
  z-index: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.swiper-cube .swiper-cube-shadow {
  position: absolute;
  left: 0;
  bottom: 0px;
  width: 100%;
  height: 100%;
  opacity: 0.6;
  z-index: 0;
}

.swiper-cube .swiper-cube-shadow:before {
  content: "";
  background: #000;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  filter: blur(50px);
}

.swiper-flip {
  overflow: visible;
}

.swiper-flip .swiper-slide {
  pointer-events: none;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  z-index: 1;
}

.swiper-flip .swiper-slide .swiper-slide {
  pointer-events: none;
}

.swiper-flip .swiper-slide-active,
.swiper-flip .swiper-slide-active .swiper-slide-active {
  pointer-events: auto;
}

.swiper-flip .swiper-slide-shadow-top,
.swiper-flip .swiper-slide-shadow-bottom,
.swiper-flip .swiper-slide-shadow-left,
.swiper-flip .swiper-slide-shadow-right {
  z-index: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.swiper-creative .swiper-slide {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
  transition-property: transform, opacity, height;
}

.swiper-cards {
  overflow: visible;
}

.swiper-cards .swiper-slide {
  transform-origin: center bottom;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
}

@font-face {
  font-family: "Play";
  src: url("../fonts/Play-Regular.woff2") format("woff2"), url("../fonts//Play-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  min-width: 320px;
}

html {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  font-family: "Play", "Arial", sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #000000;
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1370px;
  margin: 0 auto;
  padding: 0 105px;
}
@media (max-width: 1099px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 959px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}

.site-nav {
  font-family: "Play", "Arial", sans-serif;
  font-size: 18px;
  line-height: 21px;
  font-weight: 400;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 40px;
  padding-top: 35px;
  align-items: start;
}
@media (max-width: 1099px) {
  .site-nav {
    column-gap: 30px;
  }
}

.site-nav__logo {
  grid-column: 1/3;
}

.site-nav__list {
  display: flex;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}
.site-nav__list--menu {
  grid-column: 4/10;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  padding-top: 10px;
}
.site-nav__list--call {
  grid-column: 10/13;
  flex-direction: column;
  align-items: end;
  gap: 10px;
}

.site-nav__item {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.site-nav__link {
  background-image: linear-gradient(currentColor, currentColor);
  background-position: 50% 100%;
  background-repeat: no-repeat;
  background-size: 0 1px;
  transition: background-size 0.3s ease;
}
.site-nav__link:hover {
  background-size: 100% 1px;
}

.hero {
  background-image: url("../img/common/canyon.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 830px;
}

.hero__wrapper {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 40px;
  padding-top: 260px;
  padding-bottom: 50px;
}

.hero__block {
  grid-column: 1/10;
}

.hero__title {
  font-family: "Play", "Arial", sans-serif;
  font-size: 48px;
  line-height: 56px;
  font-weight: 400;
  margin-top: 0;
}

.hero__description {
  font-family: "Play", "Arial", sans-serif;
  font-size: 24px;
  line-height: 28px;
  font-weight: 400;
  letter-spacing: -0.72px;
  margin-top: 0;
  margin-bottom: 45px;
}

.hero__social {
  grid-column: 12/13;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 40px;
}

.social {
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding-left: 40px;
}

.social__item {
  list-style-type: none;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #ffffff;
  width: 40px;
  height: 40px;
  transition: all 0.3s ease;
}
@media (hover: hover) {
  .social__link:hover:not(.focus-visible) {
    filter: drop-shadow(0 2px 15px #b6b6b6);
  }
}
.social__link.focus-visible:focus {
  filter: drop-shadow(0 2px 15px #b6b6b6);
}

.about {
  padding-top: 90px;
  padding-bottom: 130px;
}

.about__wrapper {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 40px;
}

.about__title {
  font-family: "Play", "Arial", sans-serif;
  font-size: 43px;
  line-height: 50px;
  font-weight: 400;
  position: relative;
  grid-column: 1/7;
  grid-row: 1/2;
  color: #d1d1d1;
  margin-top: 0;
  margin-bottom: 45px;
}
.about__title::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 125px;
  background-color: #ffbf1a;
  left: 0;
  bottom: -12px;
}

.about__description {
  font-family: "Play", "Arial", sans-serif;
  font-size: 23px;
  line-height: 27px;
  font-weight: 400;
  grid-column: 1/7;
  grid-row: 2/3;
  color: #d1d1d1;
  margin-top: 0;
  margin-bottom: 30px;
}

.about__list {
  font-family: "Play", "Arial", sans-serif;
  font-size: 23px;
  line-height: 27px;
  font-weight: 400;
  grid-column: 1/7;
  grid-row: 3/4;
  align-self: end;
  color: #d1d1d1;
  margin-top: 0;
  margin-bottom: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 40px;
}

.about__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 13px;
  padding: 27px 25px 32px;
  border: 1px solid #ffbf1a;
  border-radius: 10px;
}

.about__number {
  text-align: center;
}

.about__word {
  font-family: "Play", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400;
  margin-left: 0;
  text-align: center;
}

.about__wrapper picture {
  grid-column: 7/13;
  grid-row: 1/4;
}

.btn {
  display: inline-flex;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    background-color: #ffffff;
    color: #000000;
  }
}
.btn.focus-visible:focus {
  background-color: #ffffff;
  color: #000000;
}

.btn--outline {
  border: 1px solid #ffffff;
  padding: 9px 15px;
  background-image: none;
}
@media (hover: hover) {
  .btn--outline:hover:not(.focus-visible) {
    background-image: none;
  }
}
.btn--outline.focus-visible:focus {
  background-image: none;
}

.btn--solid {
  font-family: "Play", "Arial", sans-serif;
  font-size: 16px;
  line-height: 10.88px;
  font-weight: 400;
  background-color: rgba(255, 191, 26, 0.87);
  border: none;
  color: #000000;
  filter: drop-shadow(0 2px 15px #b6b6b6);
  padding: 19px 35px 21px;
}

